import React, { useState, useEffect } from 'react';
import { NavHashLink as NavLink } from 'react-router-hash-link';
import { Navbar, Nav, NavbarBrand, Collapse, NavItem, Media } from 'reactstrap';
import { Link, scroller as scroll } from 'react-scroll';
import SocialNetwork from './SocialNetwork';
import logo from '../assets/images/logos/logo_oc.svg';
import home from '../assets/images/pictos/home.svg';

import '../scss/Header.scss';

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);

  const toggleNavbar = () => setIsOpen(!isOpen);
  const closeMenu = () => setIsOpen(false);

  const scrollToTop = () => {
    scroll.scrollToTop();
  };

  const scrollTo = id => {
    if (window.location.pathname === '/') {
      scroll.scrollTo(`${id}`, {
        duration: 750,
        // delay: 100,
        smooth: true
        // activeClass: 'active'
      });
    }
  };

  return (
    <Navbar expand='lg' fixed='top' className='header-container'>
      <NavbarBrand tag={Link} to='/'>
        <Media
          object
          src={logo}
          alt='logo olivier chabot'
          className='logo-oc'
          onClick={scrollToTop}
        />
      </NavbarBrand>
      <button
        type='button'
        className={`btn-menu ${isOpen ? 'active' : 'not-active'}`}
        onClick={toggleNavbar}
      >
        <span />
        <span />
        <span />
      </button>
      <Collapse isOpen={isOpen} navbar>
        <Nav className='ml-auto' navbar>
          <NavItem onClick={closeMenu}>
            <NavLink
              className='nav-link'
              to='/#home'
              smooth
              onClick={() => scrollTo('home')}
            >
              <Media object src={home} alt='home' className='picto-home' />
            </NavLink>
          </NavItem>
          <NavItem onClick={closeMenu}>
            <NavLink
              className='nav-link'
              to='/#bio'
              smooth
              onClick={() => scrollTo('bio')}
            >
              BIO
            </NavLink>
          </NavItem>
          <NavItem onClick={closeMenu}>
            <NavLink
              className='nav-link'
              // activeClass='active'
              to='/#projets'
              smooth
              onClick={() => scrollTo('projets')}
            >
              PROJETS
            </NavLink>
          </NavItem>
          <NavItem onClick={closeMenu}>
            <NavLink
              className='nav-link'
              // activeClass='active'
              to='/#contact'
              smooth
              onClick={() => scrollTo('contact')}
            >
              CONTACT
            </NavLink>
          </NavItem>
          <NavItem>
            <SocialNetwork />
          </NavItem>
        </Nav>
      </Collapse>
    </Navbar>
  );
}
